import React, { useState } from "react";
import { numSolved, getNumSolved } from "./util";
import Home from "./home";
import Steps from "./steps/steps";
import End from "./finish";

const HOME = "home";
const SOLVING = "solving";
const SOLVED = "solved";

export default () => {
  const numSolved = 0;
  // const [state, setState] = useState(HOME);
  const [state, setState] = useState(SOLVING);
  switch (state) {
    case HOME:
      return (
        <Home started={numSolved > 0} go={() => setState(SOLVING)} />
      );
    case SOLVING:
      return (
        <Steps startingStep={numSolved} onComplete={() => setState(SOLVED)} />
      );
    default:
      return <End />;
  }
}